<script>
import orderStatus from '../order-status'
export default {
    name: 'table-cart',
    props: {
        list: {
            type: Array,
            default() {
                return []
            },
        },
    },
    components: {
        orderStatus,
    },
    data() {
        return {
            detailData: {},
        }
    },
    created() {},
    mounted() {},
    methods: {
        unOrder(data) {
            this.detailData = data
            this.$refs.orderStatusId.openDialog()
        },
        okOrder(data) {
            this.detailData = data
            this.$refs.orderStatusId.accept()
        },
        // 查看详情
        seeDetail(value) {
            this.$router.push({
                path: '/logisstics-commission/orderDetail',
                query: {
                    // lineHeaderData: JSON.stringify(val.location),
                    orderCode: value.orderCode,
                },
            })
        },
    },
}
</script>

